





























































































































































































































































































































































































































@use "~main/theme"

.print-card
  margin-top: var(--gap-between-blocks)

.card-header
  display: flex
  flex-flow: row nowrap
  justify-content: space-between
  align-items: center

.seg-control-icon, ::v-deep .seg-control-icon img
  height: 1.2rem

.feedback-section
  background-color: rgb(var(--color-surface-secondary))

.bounce-enter-active
  animation: bounce-in .5s

.bounce-leave-active
  animation: bounce-in .5s reverse

@keyframes bounce-in
  0%
    transform: scale(0)
  50%
    transform: scale(1.5)
  100%
    transform: scale(1)

.help-text
  line-height: 1.2

.bg-warning.alert-banner
  position: static
  display: block

.archived-info
  position: absolute
  width: 100%
  z-index: 10
  bottom: 0
  left: 0
  background-color: rgba(0,0,0,.6)
  text-align: center
  padding: 10px 0

::v-deep .vjs-poster, ::v-deep .video-js
  background-color: #000
